.proffesional-services-container {
  h1 {
    color: $clr2;
    font-family: roboto-bold;
    font-size: 2rem;
  }
}
.proffesional-services {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 35px;
  margin-bottom: 50px;
  position: relative;

  .category-container {
    color: white;
    text-shadow: 0 0 6px #00000075;
    position: absolute;
    top: 1rem;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1.5rem;
    font-weight: 600;
    padding-inline-start: 1rem;
    .subtitle {
      font-family: Arial, Helvetica, sans-serif;
      font-size: 1rem;
      font-weight: 400;
    }
  }
  .img-container {
    transition: 0.2s;
    cursor: pointer;
    width: 100%;
    height: 250px;
    position: relative;
    
    @include for-home-category-narrow-layout {
        height: 345px;
    }
    span.on-click {
      border-radius: 4px;
      display: inline-block;
      position: absolute;
      width: 100%;
      height: 100%;
      background-color: rgba(255, 255, 255, 0);
      z-index: 5;
      transition: 0.2s;
    }
    span:hover {
      background-color: rgba(255, 255, 255, 0.164);
    }
    img {
      object-fit: cover;
      border-radius: 4px;
      width: 100%;
      height: 100%;
    }
  }
}
